<script>
import api from '@/api'
import { getToken } from '@/api/auth'

export default {
  data () {
    return {
      self: this,
      formValidate: {
        describe: '',
        status: "1",
        url: '',
        gimg: '',
        gimg_url: '',
        remark: '',
        adType: '1',
        no: '1',
        hlight: '',
        adimg: ''
      },
      ruleValidate: {
        no: [
            { required: true, message: '排序不能为空', trigger: 'blur' }
        ]
      },
      curImgMsg: {},
      configData: {},
      defaultImg: '',
      columns: [
          {
              title: '资源id',
              key: 'id',
              align: 'center',
          },
          {
              title: '资源名称',
              key: 'name',
              align: 'center',
          },
          {
            title: '操作',
            key: 'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  domProps: { // 修改标签默认内容
                    innerHTML: "删除"
                  },
                  props: {
                      type: 'error',
                      size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.index)
                    }
                  }
                }, 'Delete'),
                h('Button', {
                  domProps: { // 修改标签默认内容
                    innerHTML: "向上"
                  },
                  props: {
                    type: 'primary',
                    size: 'small',
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      const index = params.index,
                            ownData = this.data[index];

                      if(index != 0) {
                        this.data.splice(index, 1);
                        this.data.splice(index-1, 0, ownData);
                        console.log(this.data)
                      }
                    }
                  }
                }, 'View'),
                h('Button', {
                  domProps: { // 修改标签默认内容
                    innerHTML: "向下"
                  },
                  props: {
                      type: 'primary',
                      size: 'small'
                  },
                  on: {
                    click: () => {
                      const index = params.index,
                            num = this.data.length-1,
                            ownData = this.data[index];
                          
                      if(index != num) {
                        this.data.splice(index, 1);
                        this.data.splice(index+1, 0, ownData);
                        console.log(this.data)
                      }
                    }
                  }
                }, 'View')
              ]);
            }
          }
      ],
      data: [],
      isShowAddGameList: false
    }
  },
  mounted () {
    const token = getToken(),
          aid = this.$route.query.aid*1,
          advtype = this.$route.query.advtype*1,
          type_c = 2,
          params = {"token": token, "aid": aid, "advtype": advtype, "type_c": type_c};

    api.advertedit(params).then(res => {
      // console.log(res)
      if(res.code == 0) {
        this.isShowAddGameList = res.data.adsType == 5 ? true : false;
        // console.log(res.data.adsType, this.isShowAddGameList)
        this.configData = res.data.config
        this.siteinfoData = res.data.siteinfo
        this.data = res.data.gameListInfo ? res.data.gameListInfo : [];
        let{ describe, status, url, type, img, no, hlight, is_web }  = res.data.siteinfo
        this.defaultImg = is_web == 1 ? res.data.config.image_url+img : img;
        
        this.formValidate = {
          describe: describe,
          status: status,
          url: url,
          gimg: '',
          gimg_url: is_web == 2 ? img : '',
          remark: '',
          adType: type,
          no: no,
          hlight: hlight,
          adimg: img
        }
        // console.log(this.configData, this.siteinfoData, this.formValidate)
      }else {
        this.$Notice.error({
          desc: res.msg
        })
      }
      
    })      
  },
  methods: {
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          let formData = new FormData();
              formData.append('token',  getToken());
              formData.append('aid', this.$route.query.aid*1);
              formData.append('adid', this.siteinfoData.adid*1);
              formData.append('advtype', this.$route.query.advtype*1);
              formData.append('type_c', 1);
              formData.append('gimg', this.curImgMsg);
              formData.append('gimg_url', this.formValidate.gimg_url);
              formData.append('status', this.formValidate.status*1);
              formData.append('no', this.formValidate.no*1);
              formData.append('hlight', this.formValidate.hlight*1);
              formData.append('describe', this.formValidate.describe);
              formData.append('url', this.formValidate.url);
              formData.append('type', this.formValidate.adType*1);
              formData.append('adimg', this.formValidate.adimg);

          if(this.isShowAddGameList) {
            let remark = '';
            if(this.data) {
              this.data.forEach(itm => {
                if(remark) {
                  remark = remark + ',' + itm.id
                }else {
                  remark += itm.id
                }
              });
              formData.append('remark', remark)
            } else {
              formData.append('remark', '')
            }
          } else {
            formData.append('remark', this.formValidate.remark)
          }
          console.log(formData.get("type"))
          let params = formData;
          api.adverteditSet(params).then(res => {
              console.log(res)
              if(res.code == 0) {
                this.$Notice.success({
                  title: "修改成功"
                })
                this.$router.push({
                  path: '/ad/advertset',
                  query: {
                    adid: this.siteinfoData.adid
                  }
                })
              }else {
                this.$Notice.error({
                  title: res.msg
                })
              }
          })
        }else {
          console.log(111)
        }
      })
    },
    handleReset (name) {
      this.$refs[name].resetFields();
    },
    getFile (e) {
      const file = e.target.files[0],
            type = file.type,
            limitType = 'jpg|jpeg|png',
            limitTypeReg = new RegExp('.('+limitType+')$');

      if (!limitTypeReg.test(type)) {
        this.$Message.warning(`请传${limitType}图片`)
      } else {
        this.curImgMsg = e.target.files[0]; 
      }
    },
    getGameNameByGameId () {
      if(this.formValidate.remark == '' ){
        this.$Message.warning({
          background: true,
          content: '请输入要查找的游戏或应用id！'
        });
      } else if (this.formValidate.adType == 0) {
        this.$Message.warning({
          background: true,
          content: '请选择广告类型！'
        });
      } else {
        const token = getToken(),
              gameid = this.formValidate.remark,
              type = this.formValidate.adType,
              params = {"token": token, "gameid": gameid, "type": type}

        api.getGameNameByGameId(params).then(res => {
          // console.log(res)
          if(res.code == 0) {
            let isHave = false;
            if(this.data) {
               isHave = this.data.some(itm => {
                return itm.id == this.formValidate.remark
              })
            } else {
              isHave = false
            }

            if(!isHave) {
              this.data.push(res.data)
            }else {
              this.$Message.warning({
                background: true,
                content: '当前游戏或应用id已经添加，请不要重复添加'
              });
            }
          } else {
            this.$Message.error({
              background: true,
              content: res.msg
            });
          }
        })
      }
    },
    remove (index) {
      this.data.splice(index, 1);
    }
  }
}
</script>
<template>
  <div>
    <h3><Icon type="ios-construct" size=24 />广告修改</h3>
    <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="90">
      <FormItem label="图片描述" prop="describe">
        <Input class="describe" v-model="formValidate.describe"></Input>
        <span>非必填选项，可不输入</span>
      </FormItem>
      <FormItem label="开启状态" prop="status">
        <RadioGroup v-model="formValidate.status">
          <Radio v-for="itm,idx in configData.status" :label="idx" :key="idx">{{itm}}</Radio>
        </RadioGroup>
      </FormItem>
      <FormItem label="高亮状态" prop="hlight">
        <RadioGroup v-model="formValidate.hlight">
          <Radio v-for="itm,idx in configData.hlight" :label="idx" :key="idx">{{itm}}</Radio>
        </RadioGroup>
      </FormItem>
      <FormItem label="超链接" prop="url">
        <Input v-model="formValidate.url"></Input>
        <p>如果图片跳转地址为非本游戏官方网站时添加，添加时地址如：<span>http://www.leshu.com/</span>否则无效</p>
      </FormItem>
      <FormItem label="图片" prop="gimg">
        <div class="uploadBox">
          <div class="upload">
            <Icon type="ios-cloud-upload-outline" size=18></Icon>
            选择文件
          </div>
          <input type="file" name="gimg" value="" @change="getFile($event)">
        </div>
        <i>{{curImgMsg.name}}</i>
        <img class="defaultImg" :src="defaultImg" alt="">
        <p>或输入图片地址：</p>
        <Input v-model="formValidate.gimg_url"></Input>
      </FormItem>
      <template>
        <FormItem v-if="isShowAddGameList" label="备注" prop="remark">
          <Input 
            class="remarkBox" 
            v-model="formValidate.remark"
            search enter-button="添加" 
            placeholder="请输入游戏或应用id" 
            @on-search='getGameNameByGameId'
            />
          <Table border :context="self" :columns="columns" :data="data"></Table>
        </FormItem>
        <FormItem v-else label="备注" prop="remark">
          <Input class="textarea" v-model="formValidate.remark" type="textarea" :autosize="{minRows: 4,maxRows: 12}"></Input>
          <p><span>如果是礼包则必须填写礼包ID 其他为非必填选项，可不输入</span></p>
        </FormItem>
      </template>
      <FormItem label="广告类型" prop="adType">
        <Select v-model="formValidate.adType">
          <Option v-for="itm,idx in configData.top_type" :value="idx" :index="idx" :key="idx">{{itm}}</Option>
        </Select>
      </FormItem>
      <FormItem label="排序" prop="no">
        <Input class="no" v-model="formValidate.no"></Input>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="handleSubmit('formValidate')">提交</Button>
        <Button @click="handleReset('formValidate')" style="margin-left: 8px">重置</Button>
      </FormItem>
    </Form>
  </div>
</template>
<style lang="scss" scoped>
  .ivu-form {
    width: 42%;
    .ivu-form-item-content {
      width: 60%;
    }
    span {
      color: orange;
      margin: 0 5px;
    }
    i {
      color: green;
      margin: 0 5px;
    }
    .ivu-select {
      width: 20%;
    }
    .no {
      width: 20%;
    }
    .textarea {
      width: 60%;
    }
    .describe {
      width: 40%;
    }
    .remarkBox {
      width: 35%;
      margin-bottom: 10px;
    }
  }
  .uploadBox {
    display: inline-block;
    width: 100px;
    height: 34px;
    border-radius: 4px;
    border: 1px dashed #ccc;
    position: relative;
    input {
      opacity: 0;
      width: 100px;
      height: 32px;
    }
    .upload {
      position: absolute;
      top: 0;
      left: 0;
      width: 100px;
      padding: 0 4px 0 6px;
    }
  }
  .defaultImg {
    float: right;
    max-width: 60px;
    margin-right: 20px;
    border: 1px dashed #ccc;
  }
</style>